@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap');

@mixin flex($display, $align, $margin-bottom) {
	display: $display;
	align-items: $align;
	margin-bottom: $margin-bottom;
}

@mixin textStyle($font-size, $font-weight, $text-shadow, $margin-bottom) {
	font-size: $font-size;
	font-weight: $font-weight;
	text-shadow: $text-shadow;
	margin-bottom: $margin-bottom;
}

@mixin removeOriginal($outline, $border, $cursor) {
	outline: $outline;
	border: $border;
	cursor: $cursor;
}

@mixin mQ($arg) {
	@media screen and (max-width: $arg) {
		@content;
	}
}

$transition: all 0.3s ease;
$primaryText: rgba(80, 69, 69, 1);

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto', sans-serif;
	background-image: url('/src/images/backgrounds/sunset2.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom center;
	height: 100%;
	overflow-x: hidden;
	transition: background-image 2s ease-in-out;
}

.container {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	opacity: 0;
	visibility: hidden;
	transition: 2s opacity ease-in-out;

	header {
		flex: 0 1 auto;
	}
	main {
		flex: 1 1 auto;
	}
	footer {
		flex: 0 1 80px;
	}
}

.introduction {
	background-image: url('/src/images/backgrounds/sunset2.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom center;
	position: absolute;
	height: 100vh;
	right: 0;
	top: 0;
	left: 0;
	bottom: 0;
	display: flex;
	flex-direction: column;
	visibility: visible;
	opacity: 0;
	transition: 1s opacity ease-in-out;
	.introduction-overlay {
		position: absolute;
		height: 100vh;
		right: 0;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: 1;
		display: none;
		background: rgba(0, 0, 0, 0.5);
		animation: opacity 1s ease-in;
	}

	.intro-question-container {
		position: relative;
		flex: 1;
		z-index: 2;
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		opacity: 1;
		transition: 1s opacity ease-in-out;

		h3 {
			@include textStyle(3.9em, 400, none, 0em);
			color: white;
		}
		.intro-question {
			transition: 1s opacity ease-in-out;
			input {
				width: 100%;
				padding-top: 10px;
				display: block;
				background: 0;
				border: 0;
				border-bottom: 3px solid #fff;
				color: #fff;
				font-size: 3.3em;
				line-height: 1.2em;
				font-weight: 500;
				outline: none;
				text-align: center;
			}
		}
		.intro-confirmation {
			visibility: hidden;
			opacity: 0;
			position: absolute;
			transition: 1s opacity ease-in-out;
			.intro-name-title {
				line-height: 1.5em;
			}
		}
		.intro-cta {
			position: absolute;
			margin-top: 16em;
			opacity: 0;
			visibility: hidden;
			transition: 1s opacity ease-in-out;
			button {
				outline: none;
				border: 2px white solid;
				padding: 0.5em 1em;
				border-radius: 20em;
				background-color: transparent;
				cursor: pointer;
				color: white;
				font-size: 1em;
				transition: 0.3s;
				&:first-child {
					margin-right: 1em;
				}
				&:hover {
					background-color: rgba(255, 255, 255, 0.1);
				}
			}
		}
	}
}

.features {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 100;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
	opacity: 0;
	animation: opacity 1.6s ease-in-out forwards;
	.features-container {
		overflow: hidden;
		height: 500px;
		width: 640px;
		background: hsl(0, 0%, 94%);
		box-shadow: 0 22px 60px rgb(0 0 0 / 60%);
		border-radius: 15px;
		position: relative;
		padding: 1.5em 2.5em;
		text-align: center;
		.title {
			color: rgba(0, 0, 0, 0.8);
			margin: 0.75em 0em 0.6em 0em;
			font-size: 2em;
			opacity: 0;
		}
		.features-intro {
			animation: fadeIn 1.5s ease-in forwards;
		}
		.subheading {
			color: rgba(0, 0, 0, 0.6);
			font-size: 1.25em;
			opacity: 0;
		}
		.features-intro-desc {
			animation: fadeIn 1.4s ease-in forwards;
			animation-delay: 0.5s;
		}
		.features-main {
			position: relative;
			z-index: 5;
			display: flex;
			.features-content {
				display: none;
				position: absolute;
				width: 100%;
				animation: zoomIn 0.7s ease-in forwards;
				h3 {
					margin: 0.4em 0em 0.4em 0em;
					opacity: 1;
				}
				p {
					opacity: 1;
				}
				.features-icon-container {
					display: flex;
					margin-top: 1.7em;
					justify-content: space-around;
					.features-icon {
						position: relative;
						display: flex;
						flex-direction: column;
						align-items: center;
						width: 100%;
						transition: 0.3s ease-in;
						h3 {
							color: rgb(240, 153, 122);
							font-weight: 500;
						}
						&::before {
							content: '';
							width: 60px;
							height: 60px;
							border-radius: 50%;
							background-color: white;
							opacity: 0.7;
							position: absolute;
							z-index: -1;
							top: 0;
						}
						&:hover {
							transform: translateY(-4px);
						}
					}
				}
			}
		}
		.features-bg {
			position: absolute;
			margin-left: auto;
			margin-right: auto;
			top: 100px;
			left: 0;
			right: 0;
			opacity: 0;
			animation: opacity 1.5s forwards;
			animation-delay: 2s;
			img {
				width: 600px;
			}
		}
		.features-footer {
			position: absolute;
			bottom: 1em;
			left: 0;
			right: 0;
			opacity: 0;
			visibility: hidden;
			animation: opacity 1s forwards;
			animation-delay: 2.5s;
			button {
				border: none;
				outline: none;
				font-size: 1em;
				background-color: rgba(240, 153, 122, 0.9);
				color: white;
				padding: 0.4em 2.5em;
				cursor: pointer;
				border-radius: 2em;
				margin-bottom: 0.25em;
				&:hover {
					background-color: rgba(240, 153, 122, 1);
				}
			}
			.dot {
				opacity: 0.3;
				font-size: 1.2em;
				display: inline-block;
				cursor: pointer;
				transition: 0.2s ease-in-out;
				&:hover {
					transform: scale(1.75);
				}
				&:first-child {
					pointer-events: none;
				}
			}
			.active {
				opacity: 0.8;
			}
		}
	}
}

.header {
	@include flex(flex, initial, 0em);
	justify-content: space-between;
	padding: 2em 3em;
	transition: opacity 1s;
	animation: opacity 1s ease-in-out;
}

.links {
	position: relative;
	.links-icon {
		font-size: 2.25em;
		color: rgb(226, 211, 211);
		cursor: pointer;
		transition: 0.3s color ease-in-out;
		&:hover {
			color: white;
		}
	}
	.nipple-wrapper {
		position: relative;
		transition: $transition;
		transform-origin: 25% 15%;
		transform: scale(0) translateY(-100px);
		&::after {
			content: '';
			position: absolute;
			display: block;
			opacity: 1;
			top: -7px;
			left: 10px;
			border-left: 7px solid transparent;
			border-right: 7px solid transparent;
			border-bottom: 7px solid white;
		}
		.links-outer-wrapper {
			width: 250px;
			position: absolute;
			left: -20px;
			border-radius: 0.35em;
			background: rgb(255, 255, 255);
			box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
			height: 137px;
			overflow: hidden;
			transition: 0.3s;
			.links-inner-wrapper {
				overflow: hidden;
			}
		}
	}
	.links-container {
		display: flex;
		transition: 0.3s ease-in-out;
		opacity: 1;
		width: 1000px;
		position: absolute;
		transform: none;
		.links-list-container {
			position: absolute;
			width: 250px;
			padding: 1em;
			left: 0%;
			right: 25%;
			transition: 0.3s;
			h3 {
				margin-bottom: 0.6em;
				color: rgba(80, 69, 69, 1);
			}
			ul {
				list-style: none;
			}
			a {
				outline: none;
				text-decoration: none;
				display: flex;
			}
			span {
				font-size: 0.95em;
				color: $primaryText;
				white-space: nowrap;
			}
			img {
				height: 17px;
				width: 17px;
				margin-right: 0.4em;
			}
			.chrome-tab {
				opacity: 0.75;
				@include flex(flex, initial, 0.5em);
				cursor: pointer;
				i {
					margin-right: 0.5em;
					color: $primaryText;
				}
				&:hover {
					opacity: 1;
				}
			}
			.links-list {
				.link {
					justify-content: space-between;
					@include flex(flex, initial, 0.5em);
					cursor: pointer;
					opacity: 0.75;
					&:hover {
						opacity: 1;
					}
					&:hover .link-options .ellipsis-wrapper {
						opacity: 1;
					}
					.link-wrapper {
						width: 100%;
					}
					.link-options {
						display: flex;
						position: relative;
						.ellipsis-wrapper {
							opacity: 0;
							position: relative;
							display: inline-flex;
							justify-content: center;
							align-items: center;
							&:hover {
								opacity: 1;
								&::after {
									content: '';
									width: 26px;
									height: 26px;
									position: absolute;
									background-color: rgba(80, 69, 69, 0.5);
									border-radius: 50%;
								}
								i {
									color: white;
									z-index: 5;
								}
							}
							i {
								font-size: 0.85em;
								color: rgba(80, 69, 69, 1);
							}
						}
						.link-dropdown {
							background-color: $primaryText;
							border-radius: 0.35em;
							padding: 0.3em 0em;
							position: absolute;
							box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
							margin-top: -50px;
							left: -80px;
							visibility: hidden;
							opacity: 0;
							.dropdown-list {
								li {
									font-size: 0.9em;
									color: white;
									padding: 0.4em 1em;
									cursor: pointer;
									&:hover {
										background-color: rgb(121, 107, 107);
									}
								}
							}
						}
					}
				}
			}
			.link-input {
				opacity: 0.75;
				cursor: pointer;
				z-index: 5;
				color: $primaryText;
				display: flex;
				i {
					margin-right: 0.55em;
				}
				span {
					font-size: 0.95em;
				}
				&:hover {
					opacity: 1;
				}
			}
		}
		.add-links-container {
			width: 250px;
			padding: 1em;
			color: $primaryText;
			position: absolute;
			transition: 0.3s;
			right: 50%;
			left: 25%;
			label {
				@include textStyle(0.9em, 500, none, 0.3em);
				display: block;
				text-transform: uppercase;
			}
			span {
				white-space: nowrap;
			}
			input {
				width: 100%;
				display: block;
				background: 0;
				border: 0;
				border-bottom: 2px solid rgba(80, 69, 69, 0.3);
				color: $primaryText;
				font-size: 1em;
				font-weight: 700;
				outline: none;
				padding: 0.25em 0em;
				&:focus {
					border-color: rgba(80, 69, 69, 0.6);
				}
				&::placeholder {
					font-size: 0.9em;
				}
			}
			i {
				position: relative;
				cursor: pointer;
				display: inline-flex;
				justify-content: center;
				align-items: center;
				font-size: 0.9em;
			}
			.back-arrow {
				margin-bottom: 0.8em;
			}
			.add-name-wrapper {
				margin-bottom: 1em;
			}
			.add-urls-wrapper {
				.urls-list {
					list-style: none;
					margin-bottom: 0.5em;
					.url {
						display: flex;
						justify-content: space-between;
						padding: 0.35em 0em;
						&:nth-child(1) {
							border-top: 1px solid rgba(80, 69, 69, 0.25);
						}
						&:last-child {
							border-bottom: 1px solid rgba(80, 69, 69, 0.25);
						}
						.url-link {
							color: $primaryText;
							text-decoration: none;
							width: 100%;
							overflow: hidden;
							text-overflow: ellipsis;
						}
						i {
							margin-left: 0.5em;
							&:hover {
								color: white;
								z-index: 5;
								&::after {
									content: '';
									width: 22px;
									height: 22px;
									position: absolute;
									background-color: rgba(80, 69, 69, 0.5);
									border-radius: 50%;
									z-index: -1;
								}
							}
						}
					}
				}
				.add-tab-wrapper {
					margin-top: 0.6em;
					font-size: 0.8em;
					opacity: 0.7;
					cursor: pointer;
					i {
						margin-right: 0.25em;
					}
					&:hover {
						opacity: 1;
					}
				}
			}
			.add-cta-wrapper {
				margin-top: 1em;
				button {
					@include removeOriginal(none, none, pointer);
					color: white;
					padding: 0.5em 0.9em;
					border-radius: 0.35em;
					background-color: rgba(80, 69, 69, 0.8);
					font-size: 0.9em;
					&:hover {
						background-color: rgba(80, 69, 69, 1);
					}
					&:nth-child(1) {
						margin-right: 0.5em;
					}
				}
			}
		}
	}
}

.second-tab {
	transform: translateX(-25%) !important;
}

.visibility {
	visibility: visible !important;
	opacity: 1 !important;
}

.weather-app {
	@include flex(flex, flex-end, 0em);
	flex-direction: column;
	color: white;
	letter-spacing: 0.05em;
	.temperature {
		@include flex(flex, flex-end, 0.4em);
		.icon-div {
			.temperature-icon {
				@include mQ(1000px) {
					width: 26px;
				}
			}
		}
		.temperature-degree {
			@include textStyle(2.5em, 700, 2px 2px 6px rgba(0, 0, 0, 0.2), -0.1em);
			margin-left: 0.2em;
		}
		.degree-icon {
			align-self: flex-start;
		}
	}
	.location {
		@include textStyle(1.5em, 500, 2px 2px 6px rgba(0, 0, 0, 0.2), 0.4em);
	}
	.search-box {
		@include removeOriginal(none, none, auto);
		@include textStyle(0.85em, 500, none, 0.8em);
		box-shadow: 5px 5px 14px rgba(0, 0, 0, 0.4);
		padding: 0.5em 0em 0.5em 1em;
		border-radius: 0.35em;
		&::placeholder {
			color: rgba(80, 69, 69, 1);
		}
	}
	.extra-info {
		@include textStyle(1.5em, normal, 2px 2px 6px rgba(0, 0, 0, 0.2), 0em);
		display: flex;
		p {
			margin-top: 0.4em;
		}
		img {
			margin-right: 0.7em;
		}
		.humidity {
			margin: 0.1em 0.7em 0em 0em;
		}
		.wind {
			margin-top: 0.1em;
		}
	}
}

.openHistoryContainer {
	transform: translate(137%, -50%) !important;
	opacity: 1 !important;
	visibility: visible !important;
}

#pomodoroDarkMode {
	background-color: rgba(14, 14, 14, 1);
	color: rgb(178, 185, 181);
	.pomodoro-theme-container {
		i {
			color: rgb(178, 185, 181);
			filter: drop-shadow(0px 0px 3px rgba(255, 255, 255, 0.5));
		}
	}
	.pomodoro-warning {
		background-color: rgba(77, 77, 77, 0.5);
		.pomodoro-warning-container {
			background-color: rgb(32, 32, 32);
		}
	}
	button {
		color: rgba(14, 14, 14, 1);
		background-color: rgb(178, 185, 181);
		&:hover {
			background-color: rgb(130, 134, 132);
		}
	}
	.showPomodoroCta {
		&::after {
			background-color: rgb(178, 185, 181);
		}
		&:hover {
			color: rgba(14, 14, 14, 1);
		}
	}
}

main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 3em 3em;
	padding-top: 0em;
	animation: opacity 1s ease-in-out;
	.pomodoro-app {
		display: none;
		color: white;
		letter-spacing: 0.05em;
		width: 100%;
		opacity: 0;
		transition: opacity 1s;
		.pomodoro-time-container {
			display: none;
			opacity: 0;
			position: absolute;
			height: 100vh;
			right: 0;
			top: 0;
			left: 0;
			bottom: 0;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			background-color: rgb(241, 241, 241);
			z-index: 499;
			color: rgb(87, 82, 85);
			transition: 1s;
			.pomodoro-theme-container {
				position: absolute;
				top: 37px;
				right: 64px;
				font-size: 1.6em;
				&:hover {
					transform: scale(1.2);
					color: rgba(34, 34, 34, 1);
					.icon-on {
						transform: rotate(180deg) scale(0.5);
						opacity: 0;
						visibility: hidden;
					}
					.icon-off {
						opacity: 1;
						visibility: visible;
						transform: rotate(0deg) scale(1);
					}
				}
				i {
					position: absolute;
					cursor: pointer;
					top: 0;
					left: 0;
					filter: drop-shadow(0px 0px 3px rgba(255, 255, 255, 1));
					display: block;
					transition: 0.3s;
					color: rgba(27, 27, 27, 0.8);
				}
				.icon-on {
					transition: opacity 0.5s, transform 0.5s;
					font-size: 1.23em;
				}
				.icon-off {
					opacity: 0;
					transform: rotate(-180deg) scale(0.5);
					transition: opacity 0.5s, transform 0.5s;
				}
			}
			.pomodoro-start {
				position: relative;
				transition: 1s opacity;
				z-index: 5;
				&:hover {
					.showBtn-container .showPomodoroCta,
					.pomodoro-cycle {
						opacity: 1 !important;
						visibility: visible;
					}
				}
				.pomodoro-cycle {
					opacity: 0;
					transition: 0.3s;
				}
				.pomodoro-time {
					@include textStyle(11em, 500, none, 0em);
					display: flex;
					justify-content: center;
				}
				.pomodoro-message {
					@include textStyle(4.1em, 500, none, 0em);
				}
				.showBtn-container {
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					width: 100%;
					.showPomodoroCta {
						cursor: pointer;
						font-size: 1.5em;
						opacity: 0;
						visibility: hidden;
						display: inline-flex;
						align-items: center;
						justify-content: center;
						width: 42px;
						height: 42px;
						border-radius: 50%;
						transition: 0.5s;
						&::after {
							position: absolute;
							content: '';
							width: 42px;
							height: 42px;
							opacity: 0;
							background-color: rgba(80, 69, 69, 0.6);
							border-radius: 50%;
							z-index: -1;
							transition: 0.5s;
						}
						&:hover {
							color: white;
							&::after {
								opacity: 1;
							}
						}
					}
				}

				.pomodoro-ctas {
					position: absolute;
					transition: 0.5s all ease-in-out;
					width: 100%;
					bottom: -95px;
					height: 0;
					overflow: hidden;
					.cta-container {
						display: flex;
						justify-content: space-around;
						width: 300px;
						margin: auto;
						.pomodoro-cta {
							color: white;
							border-radius: 0.25em;
							border: none;
							outline: none;
							padding: 0.25em;
							width: 100px;
							cursor: pointer;
							font-size: 1.5em;
							background-color: rgb(87, 82, 85);
							transition: 0.3s;
							&:hover {
								background-color: rgb(141, 135, 139);
							}
						}
					}
				}
			}
			.pomodoro-warning {
				display: flex;
				opacity: 0;
				visibility: hidden;
				position: absolute;
				height: 100vh;
