		.pomodoro-settings-container {
			position: absolute;
			color: rgba(87, 82, 85, 0.8);
			background-color: white;
			width: max-content;
			margin: auto;
			text-align: left;
			border-radius: 1em;
			top: 50%;
			right: 0;
			left: 0;
			box-shadow: 8px 8px 30px rgba(87, 82, 85, 0.5);
			transform: translate(0%, -50%);
			z-index: 501;
			transition: opacity 1s;
			.pomodoro-settings-heading {
				padding: 1em 2em 1em 2em;
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom: 1px solid rgba(87, 82, 85, 0.5);
				h1 {
					font-size: 1.5em;
				}
				i {
					cursor: pointer;
					color: rgba(87, 82, 85, 0.8);
					transition: 0.3s;
					&:hover {
						color: rgba(87, 82, 85, 1);
					}
				}
			}
			.pomodoro-settings-main {
				padding: 0em 2em;
				h3 {
					font-size: 1em;
					padding-top: 1em;
				}
				.pomodoro-options {
					display: grid;
					grid-template-columns: repeat(3, 1fr);
					gap: 1.5em;
					padding: 1em 0em;
					padding-bottom: 1.5em;
					span {
						font-size: 0.72em;
					}
					input {
						margin-top: 0.3em;
						display: block;
						width: 100px;
						border-radius: 0.25em;
						border: none;
						background-color: rgba(87, 82, 85, 0.15);
						padding: 0.6em 1em;
						outline: none;
						color: rgb(87, 82, 85);
						font-weight: 500;
					}
					.switch {
						position: relative;
						display: inline-block;
						width: 37px;
						height: 20px;
					}
					.switch input {
						opacity: 0;
						width: 0;
						height: 0;
					}
					.slider {
						position: absolute;
						cursor: pointer;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						background-color: rgba(87, 82, 85, 0.3);
						transition: 0.4s;
					}
					.slider:before {
						position: absolute;
						content: '';
						height: 16px;
						width: 16px;
						left: 3px;
						bottom: 2px;
						background-color: white;
						transition: 0.4s;
					}
					input:checked + .slider {
						background-color: rgb(87, 82, 85);
					}
					input:focus + .slider {
						box-shadow: 0 0 1px rgb(87, 82, 85);
					}
					input:checked + .slider:before {
						transform: translateX(15px);
					}
					/* Rounded sliders */
					.slider.round {
						border-radius: 120px;
					}
					.slider.round:before {
						border-radius: 50%;
					}
					i {
						font-size: 1.3em;
						margin-right: 0.4em;
					}
					.pomodoro-option-six {
						p {
							font-size: 0.8em;
							background-color: rgba(87, 82, 85, 0.15);
							width: 100%;
							text-align: center;
							padding: 0.25em;
							border-radius: 0.25em;
							cursor: pointer;
							transition: 0.3s;
							&:hover {
								background-color: rgba(87, 82, 85, 0.3);
							}
						}
					}
					.pomodoro-option-four,
