				.pomodoro {
					opacity: 1 !important;
				}
			}
			.time-container-left {
				display: inline-flex;
				flex: 1 0 50px;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				.pomodoro-icon {
					margin-right: 1.5em;
					margin-top: 1em;
					.pomodoro {
						cursor: pointer;
						font-size: 1.5em;
						opacity: 0;
						display: inline-flex;
						align-items: center;
						justify-content: center;
						position: relative;
						width: 42px;
						height: 42px;
						border-radius: 50%;
