			}
			.greeting-ellipsis-wrapper {
				margin-left: 6px;
				margin-top: 10px;
				position: relative;
				i {
					cursor: pointer;
					font-size: 1.25em;
					opacity: 0;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					position: relative;
					width: 35px;
					height: 35px;
					border-radius: 50%;
					&::after {
						position: absolute;
						content: '';
						width: 35px;
						height: 35px;
						opacity: 0;
						background-color: rgba(80, 69, 69, 0.6);
						border-radius: 50%;
						z-index: -1;
					}
					&:hover {
						&::after {
							opacity: 1;
						}
					}
				}
				.ellipsis-dropdown {
					background-color: rgba(80, 69, 69, 0.6);
					text-align: left;
					border-radius: 0.35em;
					padding: 0.3em 0em;
					position: absolute;
					min-width: max-content;
					top: 41px;
					right: -119px;
					z-index: 5;
					box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
					opacity: 0;
					transform-origin: 15% 20%;
					transform: scale(0) translateY(-100px);
					transition: 0.3s ease-in;
					&::after {
						content: '';
						display: block;
						position: absolute;
						left: 26px;
						margin: 0px auto;
						top: -6px;
						height: 0;
						width: 0;
						border-left: 6px solid transparent;
						border-right: 6px solid transparent;
						border-bottom: 6px solid rgba(80, 69, 69, 0.6);
					}
					.ellipsis-options {
