					right: 12px;
					top: -56px;
					height: 0;
					width: 0;
					border-left: 7px solid transparent;
					border-right: 7px solid transparent;
					border-top: 7px solid white;
				}
			}
			.tobe-app {
				bottom: 56px;
				right: -20px;
				position: absolute;
				background-color: white;
				width: 320px;
				border-radius: 0.35em;
				text-align: left;
				overflow: hidden;

				&__ellipsis-wrapper {
					font-size: 0.8em;
					i {
						position: relative;
						display: inline-flex;
						align-items: center;
						opacity: 0;
						justify-content: center;
						cursor: pointer;
						&::after {
							content: '';
							width: 22px;
							opacity: 0;
							height: 22px;
							position: absolute;
							background-color: rgba(80, 69, 69, 0.5);
							border-radius: 50%;
							z-index: -1;
						}
						&:hover {
							color: white;
							z-index: 5;
							&::after {
								opacity: 1;
							}
						}
					}
				}
				&__header {
					position: relative;
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding: 1.5em 1.25em;
					height: 25px;
					cursor: pointer;
					&:hover .tobe-app__projects .tobe-app__ellipsis-wrapper {
						opacity: 1;
					}
				}
				&__projects {
					display: flex;
					align-items: center;
					flex: 1 1 auto;
					&:hover {
						i {
							color: white;
							z-index: 5;
							&::after {
								opacity: 1;
							}
						}
					}
					&__name {
						font-size: 1.3em;
						font-weight: 500;
						margin-right: 0.5em;
						outline: none;
					}
					i {
						opacity: 1;
						margin-top: 4px;
						&::after {
							top: -6px;
						}
					}
					&__nipple-wrapper {
						position: absolute;
						top: 46px;
						display: none;
						z-index: 100;

						&::after {
							content: '';
							position: absolute;
							display: block;
							opacity: 1;
							top: -7px;
							left: 22px;
							border-left: 7px solid transparent;
							border-right: 7px solid transparent;
							border-bottom: 7px solid rgb(87, 82, 85);
						}
					}
					&__list {
						background-color: rgb(87, 82, 85);
						color: rgb(190, 190, 190);
						border-radius: 0.4em;
						font-size: 0.9em;
						padding: 0.4em 0em;
						min-width: 240px;
						max-height: 310px;
						overflow-y: auto;
						&::-webkit-scrollbar {
							width: 12px;
						}
						&::-webkit-scrollbar-track {
							background: transparent;
						}
						&::-webkit-scrollbar-thumb {
							border: 4px solid rgba(0, 0, 0, 0);
							background-clip: padding-box;
							border-radius: 9999px;
							background-color: #aaa;
						}
						&__item,
						.tobe-app__projects__list__add {
							padding: 6px 1em;
							&--active {
								color: white;
								.tobe-app__projects__list__count {
									display: inline-block;
								}
							}
							&:hover {
								background-color: rgb(110, 107, 109);
							}
						}
						&__count {
							font-size: 0.875em;
							margin-left: 0.5em;
							pointer-events: none;
						}
						&__add-wrapper {
							display: flex;
							align-items: center;
							i {
								font-size: 0.7em;
								margin-right: 0.5em;
							}
							span {
								font-size: 0.9em;
							}
						}
					}
				}
				&__options {
					display: none;
					i {
						opacity: 1;
					}
					&:hover {
						i {
							&::after {
								opacity: 1;
							}
						}
					}
					&__dropdown-wrapper {
						background-color: rgb(87, 82, 85);
						color: rgb(206, 206, 206);
						position: absolute;
						border-radius: 0.4em;
						top: 46px;
						z-index: 10;
						font-size: 0.85em;
						right: 6px;
						padding: 5px 0px;
						display: none;
						&::after {
							content: '';
							position: absolute;
							display: block;
							opacity: 1;
							top: -7px;
							right: 13px;
							border-left: 7px solid transparent;
							border-right: 7px solid transparent;
							border-bottom: 7px solid rgb(87, 82, 85);
						}
					}
					&__dropdown {
						&__item {
							padding: 0.35em 1em;
							&:hover {
								background-color: rgb(110, 107, 109);
							}
						}
						&__line {
							height: 1px;
							margin: 5px 0;
							padding: 0em;
							background-color: white;
						}
					}
				}
				&__item {
					display: flex;
					padding: 0.25em 1.25em;
					&:hover {
						i {
							opacity: 1;
						}
					}
					&__checkbox {
						position: relative;
						top: 2px;
						cursor: pointer;
						margin-right: 0.6em;
					}
					input[type='checkbox']:checked + .tobe-app__item__name {
						text-decoration: line-through;
						opacity: 0.7;
					}
					&__name {
						flex: 1 1 auto;
						outline: none;
						transition: 0.5s;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
					&__options {
						position: relative;
						display: flex;
					}
				}
				&__list-wrapper {
					max-height: 400px;
					overflow-y: auto;
					overflow-x: hidden;
					transition: 0.3s;
					&::-webkit-scrollbar {
						width: 16px;
					}
					&::-webkit-scrollbar-track {
						background: transparent;
					}
					&::-webkit-scrollbar-thumb {
						border: 4px solid rgba(0, 0, 0, 0);
						background-clip: padding-box;
						border-radius: 9999px;
						background-color: #aaa;
					}
				}
				&__dropdown-wrapper {
					background-color: rgb(87, 82, 85);
					color: rgb(206, 206, 206);
					position: absolute;
					box-shadow: 2px 2px 8px rgb(0 0 0 / 30%);
					border-radius: 0.35em;
					top: -4px;
					z-index: 10;
					font-size: 0.8em;
					right: 22px;
					width: max-content;
					padding: 5px 0px;
					display: none;
				}
				&__dropdown {
					max-height: 168px;
					overflow: auto;
					&::-webkit-scrollbar {
						width: 10px;
					}
					&::-webkit-scrollbar-track {
						background: transparent;
					}
					&::-webkit-scrollbar-thumb {
						border: 3px solid rgba(0, 0, 0, 0);
						background-clip: padding-box;
						border-radius: 9999px;
						background-color: #aaa;
					}
					&__item {
						padding: 0.5em 1em;
						cursor: pointer;
						&:hover {
							background-color: rgb(110, 107, 109);
						}
					}
					&__line {
						height: 1px;
						background-color: rgb(168, 168, 168);
					}
				}
				&__footer {
					margin-top: 0.25em;
					padding: 1em 1.25em;
					padding-top: 0em;
					&__input {
						padding-top: 10px;
						display: block;
						background: 0;
						width: 100%;
						border: 0;
						color: rgb(87, 82, 85);
						font-size: 1em;
						font-weight: 500;
						outline: none;
					}
					.todoProject {
						display: none;
					}
				}
			}
		}
	}
}

.share {
	position: relative;
	.share-box {
		transform-origin: 50% 150%;
		transform: scale(0) translateY(-30px);
		opacity: 0;
		position: absolute;
		bottom: 25px;
		left: -108px;
		transition: 0.5s;
		ul {
			position: relative;
			display: flex;
			color: #fff;
			height: 34px;
			width: 228px;
			background: white;
			border-radius: 0.4em;
			align-items: center;
			list-style: none;
			&::after {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				right: 0;
				margin: 0px auto;
				bottom: -6px;
				height: 0;
				width: 0;
				border-left: 6px solid transparent;
				border-right: 6px solid transparent;
				border-top: 6px solid white;
			}
			a {
				display: inline-block;
				position: relative;
				text-decoration: none;
				margin-right: 1.25em;
				&:nth-child(1) {
					margin-left: 0.725em;
				}
				i {
					font-size: 20px;
					cursor: pointer;
					text-align: center;
					color: rgba(80, 69, 69, 0.8);
					&:hover {
						color: rgba(80, 69, 69, 1);
					}
				}
				&:hover span {
					visibility: visible;
					opacity: 1;
				}
				span {
					background-color: rgba(80, 69, 69, 0.8);
					position: absolute;
					top: -28px;
					left: -55px;
					padding: 0.25em 0.5em;
					color: white;
					font-size: 0.8em;
					visibility: hidden;
					opacity: 0;
					text-align: center;
					display: inline-block;
					width: 130px;
					border-radius: 0.25em;
					transition: 0.3s;
					&::after {
						content: '';
						position: absolute;
						top: 100%;
						left: 50%;
						margin-left: -5px;
						border-width: 5px;
						border-style: solid;
						border-color: rgba(80, 69, 69, 0.8) transparent transparent
							transparent;
					}
				}
			}
		}
	}
}

.background-show {
	transform: translateZ(0) translateY(-8px) !important;
}

.completed {
	transition: opacity 0.5s ease;
	text-decoration: line-through;
	opacity: 0.5;
	animation: strike 0.5s linear;
}

.fall {
	transform: translateY(8rem) rotateZ(0deg);
	opacity: 0;
}

.warning {
	animation: warning 0.6s ease-in-out;
}

.important {
	animation: important 0.6s ease-in-out;
}

@keyframes strike {
	from {
		text-decoration-color: transparent;
	}
	to {
		text-decoration-color: auto;
	}
}

@keyframes opacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		visibility: visible;
	}
}

@keyframes warning {
	0% {
		background: rgba(99, 99, 96, 0.5);
	}
	100% {
		background: rgba(99, 99, 96, 0);
	}
}

@keyframes important {
	0% {
		background: rgba(255, 255, 255, 0.5);
	}
	100% {
		background: rgba(255, 255, 255, 0.2);
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
		transform: translateY(65px);
	}
	100% {
		opacity: 1;
		transform: translateY(0px);
	}
}

@keyframes fadeOut {
	0% {
		opacity: 1;
		transform: scale(1);
	}
	100% {
		opacity: 0;
		transform: scale(1.1);
	}
}

@keyframes swipeIn {
	0% {
		transform: translateY(0%);
	}
	100% {
		transform: translateY(50%);
	}
}

@keyframes swipeLeft {
	0% {
		transform: translateX(150%);
	}
	100% {
		transform: translateX(0%);
	}
}

@keyframes zoomIn {
	0% {
		opacity: 0;
		transform: scale(0.9) translateY(20px);
	}
	100% {
		opacity: 1;
		transform: scale(1) translateY(0px);
	}
}

.share-open {
	opacity: 1 !important;
	transform: scale(1) translateY(0px) !important;
}

.display {
	display: block !important;
}

.highlight {
	background-color: rgb(211, 211, 211);
}
